import Vue from 'vue'
import Router from 'vue-router'

import Portfolio from '../views/Portfolio'
import Skills from '../views/Skills'
import Main from '../components/Main'
import Contact from '../views/Contact'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio
    },
    {
      path: '/skills',
      name: 'Skills',
      component: Skills
    },
    {
      path: '/contact',
      name: 'Contact',
      component: Contact
    }
  ]
})
